#skill {
    width: 90%;
    text-align: center;
    margin: auto;
    padding: 4%;
    margin-top: 5%;
    box-sizing: border-box;
    /* border-radius: 4%; */
    scroll-margin-top: 60px;
    background-color: #131f33;
  }
  .h15 {
    /* padding-top: 6%; */
    font-family:'Roboto', sans-serif;
    color:white;
    margin-bottom: 2%;
    font-size: 36px;
  }
  #tools {
    display: flex;
    margin: auto;
    justify-content: space-between;
    flex-wrap: wrap;
    row-gap: 30px;
    padding: 40px 0;
    box-sizing: border-box;
  }
  #tools div {
  
    background-color: white;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    background-size: 200% 200%;
    border-radius: 15%;
    width: 70px;
    height: 70px;
    font-size: 14px;
    font-family: sans-serif;
    color: #131f33;
    padding: 30px;
    padding-top:15px;
    
  }
  #tools1 {
    display: flex;
    margin: auto;
    justify-content: space-evenly;
    /* gap: 10px; */
    flex-wrap: wrap;
    row-gap: 30px;
    padding: 40px 0;
    box-sizing: border-box;
  }
  #tools1 div {
  
    background-color: white;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
    background-size: 200% 200%;
    border-radius: 15%;
    width: 70px;
    height: 70px;
    font-size: 14px;
    font-family: sans-serif;
    color: #131f33;
    padding: 30px;
    padding-top:15px;
    
  }
  /* #skill h1 {
    margin: 10px;
  } */
  @media only screen and (min-width:320px) and (max-width:480px){
    #tools div {
  
      
      background-size: 200% 200%;
      border-radius: 15%;
      width: 100px;
      height: 70px;
      font-size: 17px;
      margin: auto;
      padding: 10px;
      padding-top:15px;
      
    }
    #tools1 div {
  
      
      background-size: 200% 200%;
      border-radius: 15%;
      width: 100px;
      height: 70px;
      font-size: 17px;
      margin: auto;
      padding: 10px;
      padding-top:15px;
      
    }
    #skill {
      
      margin-top: 10%;
     
    }
  } 
